const getQueryString = (data) => {
  return data
    ? `?${Object.keys(data).map(k => `${k}=${data[k]}`).join('&')}`
    : ''
}

const request = (url, data) => new Promise(async(resolve) => {
  const res = await fetch(`/` + url + getQueryString(data))
  const respData = await res.json()
  resolve(respData)
})

const requestPost = (url, data) => new Promise(async(resolve) => {
  const res = await fetch(`/` + url, {
    method: 'POST',
    headers: {
      'content-type': 'application/json',
    },
    body: JSON.stringify(data),
  })
  const respData = await res.json()
  resolve(respData)
})

export const getTicket = (data) => request('/ticket', data)
export const getSymbol = (data) => request('/symbol', data)
export const getDetails = (data) => request('/details', data)
export const getQuote = (data) => request('/quote', data)
export const getNews = (data) => request('/news', data)
export const getRecommend = (data) => request('/recommend', data)
export const getInsight = (data) => request('/insight', data)
export const getPeers = (data) => request('/peers', data)
export const getEarnings = (data) => request('/earnings', data)
export const getUser = (data) => request('/user', data)
export const getWatch = (data) => request('/watch', data)
export const getStock = (data) => request('/stock', data)

export const postUser = (data) => requestPost('/user', data)
export const postWatch = (data) => requestPost('/watch', data)
export const postBuy = (data) => requestPost('/buy', data)
export const postSell = (data) => requestPost('/sell', data)
