const createStackedOptions = (recommend) => {

  return {
    chart: {
      type: 'column',
      backgroundColor: '#F4F4F4', // Set the background color here
    },
    title: {
      text: 'Recommendation Trends',
    },
    colors: ['#136217', '#3DC92E', '#93500E', '#CE4E07', '#311B04'],
    xAxis: {
      //4
      categories: [recommend.map(item => item.period)],
    },
    yAxis: {
      min: 0,
      title: {
        text: 'Percent',
      },
    },
    plotOptions: {
      column: {
        stacking: 'normal',
        dataLabels: {
          enabled: true,
        },
      },
    },
    series: [
      {
        name: 'Strong Buy',
        data: recommend.map(item => item.strongBuy),
      }, {
        name: 'Buy',
        data: recommend.map(item => item.buy),
      }, {
        name: 'Hold',
        data: recommend.map(item => item.hold),
      }, {
        name: 'Sell',
        data: recommend.map(item => item.sell),
      },
      {
        name: 'Strong Sell',
        data: recommend.map(item => item.strongSell),
      },
    ],
  }
}

const createLine = (data) => ({
  chart: {
    type: 'spline',
    backgroundColor: '#F4F4F4', // Set the background color here

  },
  title: {
    text: 'Historical EPS Surprise',
  },
  xAxis: {
    labels: {
      formatter: function() {
        const label = this.value
        // Split label at a specific character or word boundary
        const parts = label.split(' ') // Adjust splitting logic as needed
        return parts.join('<br>')
      },
    },
    categories: data.map(item => `${item.period} surprise:${item.surprise}`),
  },
  yAxis: {
    title: {
      text: 'Quantity EPS',
    },
    labels: {
      format: '{value}',
    },
  },
  tooltip: {
    crosshairs: true,
    shared: true,
  },
  plotOptions: {
    spline: {
      marker: {
        radius: 4,
        lineColor: '#666',
        lineWidth: 1,
      },
    },
  },
  series: [
    {
      name: 'Actual',
      marker: {
        symbol: 'diamond',
      },
      data: data.map(item => item.actual),
    },
    {
      name: 'Estimate',
      marker: {
        symbol: 'square',
      },
      data: data.map(item => item.estimate),

    },
  ],
})
export default ({ symbol, search }) => {
  const [insights] = useRequest(() => getInsight({ search }))
  const [recommend] = useRequest(() => getRecommend({ search }))
  const [earnings] = useRequest(() => getEarnings({ search }))

  const [total, setTotal] = useState([])

  useEffect(() => {
    if (insights) {
      const data = [
        { name: 'Total', mspr: 0, change: 0 },
        { name: 'Positive', mspr: 0, change: 0 },
        { name: 'Negative', mspr: 0, change: 0 },
      ]

      const aggregateData = (key) => insights.reduce((
        acc,
        insight,
      ) => acc + insight[key], 0)
      const aggregate = (
        key,
        isPositive = true,
      ) => insights.filter(item => isPositive ? item[key] > 0 : item[key] < 0)
      .reduce((acc, insight) => acc + insight[key], 0)
      // Calculate totals
      data[0].mspr = aggregateData('mspr')
      data[0].change = aggregateData('change')
      data[1].mspr = aggregate('mspr', true)
      data[1].change = aggregate('change', true)
      data[2].mspr = aggregate('mspr', false)
      data[2].change = aggregate('change', false)
      setTotal(data)
    }
  }, [insights])
  return insights && recommend && earnings ? <div>
    <div>
      <h4>Insider Sentiments</h4>
      <div className="row">
        <div className="col-md-6 col-sm-12 mx-auto">
          <table className="table">
            <thead>
            <tr>
              <th scope="col">{symbol.name}</th>
              <th scope="col">MSPR</th>
              <th scope="col">Change</th>
            </tr>
            </thead>
            <tbody>
            {total.map((item) => (
              <tr key={item.name}>
                <td>{item.name}</td>
                <td>{item.mspr}</td>
                <td>{item.change}</td>
              </tr>
            ))}
            </tbody>
          </table>
        </div>
      </div>
      <div className={'row'}>
        <div className={'col-md-6 col-sm-12'}>
          <HighchartsReact
            highcharts={Highcharts}
            options={createStackedOptions(recommend)}
          />
        </div>
        <div className={'col-md-6 col-sm-12'}>
          <HighchartsReact
            highcharts={Highcharts}
            options={createLine(earnings)}
          />
        </div>
      </div>
    </div>
  </div> : <div className="spinner-border text-primary" role="status">
    <span className="visually-hidden">Loading...</span>
  </div>
}
