import { useEffect, useState } from 'react'
import { getQuote, getStock, getSymbol, getUser, getWatch } from '../../api'
import UseRequest from '../../hook/useRequest'
import { StockModal } from '../Tickets'

const WatchItem = ({ updateUser }) => {
  const [stocks, updateStocks] = UseRequest(() => getStock())
  const [list, setList] = useState([])
  const fetchData = async() => {

    const list = await Promise.all(stocks.map(async item => {
      const data = await getSymbol({ search: item.name })
      const quote = await getQuote({ search: item.name })
      return {
        symbol: data,
        quote,
        stock: item,
        quantity: item.quantity,
        avgCostPerShare: '107.24',
        totalCost: parseFloat(item.quantity) * parseFloat(item.price),  // Calculate total cost
        change: quote.dp,  // Extract daily percentage change
        currentPrice: quote.c || item.price,  // Use quote.c or stock.price for current price
        marketValue: parseFloat(item.quantity) * parseFloat(quote.c),  // Calculate market value

      }
    }))
    console.log(list)
    setList(list)
  }
  useEffect(() => {
    stocks && stocks.length && fetchData()
  }, [stocks])
  return <div>
    {list.map(item => <div className="card mb-4">
      <div className="card-header">
        <h3>{item.symbol.ticker}
          <span className={'h5'}>{item.symbol.name}</span></h3>
      </div>
      <div className="card-body">
        <div className={'row'}>
          <div className={'col-6'}>
            <div>Quantity: {item.quantity}</div>
            <div>Avg. Cost/Share: {item.avgCostPerShare}</div>
            <div>Total Cost: {item.totalCost}</div>
          </div>
          <div className={'col-6'}>
            <div>Change: {item.change}</div>
            <div>Current Price: {item.currentPrice}</div>
            <div>Market Value: {item.marketValue}</div>
          </div>
        </div>
      </div>
      <div className="card-footer text-body-secondary">
        <button
          data-bs-toggle="modal"
          data-bs-target="#StockModal" className={'btn btn-primary me-2'}
        >Buy
        </button>
        <button
          data-bs-toggle="modal"
          data-bs-target="#SellModal" className={'btn btn-danger'}
        >Sell
        </button>

      </div>
      <StockModal
        symbol={item.symbol}
        quote={item.quote}
        onSuccess={() => {
          updateStocks()
          updateUser()
        }}
      ></StockModal>
      <StockModal
        symbol={item.symbol}
        quote={item.quote}
        isSelling
        onSuccess={() => {
          updateStocks()
          updateUser()
        }}
      ></StockModal>

    </div>)}
  </div>
}
export default () => {
  const [user, updateUser] = UseRequest(() => getUser())

  return <div className={'container'}>
    <h2>My Portfolio</h2>
    <h3>Money in Wallet: ${user ? user.price : 0}</h3>
    <WatchItem updateUser={updateUser} />
  </div>
}
