import { useEffect, useState } from 'react'
import { getQuote, getSymbol, getTicket, getWatch, postWatch } from '../../api'
import UseRequest from '../../hook/useRequest'

const WatchItem = () => {
  const [watch, updateWatch] = UseRequest(() => getWatch())
  const [list, setList] = useState([])
  const fetchData = async() => {

    const list = await Promise.all(watch.map(async item => {
      const data = await getSymbol({ search: item.name })
      const quote = await getQuote({ search: item.name })
      return {
        symbol: data,
        quote,
      }
    }))
    setList(list)
  }
  const handleCancel = (item) => {
    postWatch({
      name: item.symbol.ticker,
      turnon: 0,
    }).then(() => updateWatch())
  }
  useEffect(() => {
    watch && fetchData()
  }, [watch])
  return <div>
    {list.length > 0 ? list.map(item => <div className="card mb-4">
      <button
        onClick={() => handleCancel(item)}
        type="button"
        className="btn-close float-start"
      ></button>
      <div className="card-body">
        <div className={'row'}>
          <div className={'col-6'}>
            <div className={'h3'}>
              {item.symbol.ticker}
            </div>
            <div className={'h5'}>
              {item.symbol.name}
            </div>
          </div>

          <div className={'col-6'}>
            <h3
              className={item.quote.d < 0
                ? 'text-danger'
                : 'text-success'}
            >{item.quote.c}</h3>
            <h4
              className={item.quote.d < 0
                ? 'text-danger'
                : 'text-success'}
            >{item.quote.d}({item.quote.dp})</h4>
          </div>
        </div>
        <div>
        </div>
      </div>
    </div>) : <>
      <div className="alert alert-warning" role="alert">
        Currently you don't have any stocks in your watchlist.
      </div>
    </>}
  </div>
}
export default () => {

  return <div className={'container'}>
    <h2>My Watchlist</h2>
    <WatchItem></WatchItem>
  </div>
}
