import Highcharts from 'highcharts'
import HighchartsReact from 'highcharts-react-official'
import { getDetails, getPeers } from '../../api'
import useRequest from '../../hook/useRequest'
import { isMarketClosed } from './index'

const chartOptions = (ticker, data, quote) => ({
  chart: {
    backgroundColor: '#F4F4F4', // Set the background color here
  },
  title: {
    text: `${ticker} Hourly Price Variation`,
  },
  xAxis: {
    type: 'datetime',
    labels: {
      overflow: 'justify',
    },
  },
  yAxis: {
    opposite: true,
    title: {
      text: null,
    },
  },
  plotOptions: {
    series: {
      label: {
        connectorAllowed: false,
      },
      pointInterval: 3600000,
      pointStart: Date.UTC(
        new Date().getFullYear(),
        new Date().getMonth(),
        new Date().getDate() - 1,
        0,
        0,
        0,
      ),
    },
  },
  series: [
    {
      lineColor: quote.d < 0 ? 'red' : 'green',
      name: 'price',
      marker: {
        enabled: false,
      },
      data: data.map(item => item.vw),
    },
  ],
  legend: {
    enabled: false,
  },
  responsive: {
    rules: [
      {
        condition: {
          maxWidth: 500,
        },
        chartOptions: {
          legend: {
            layout: 'horizontal',
            align: 'center',
            verticalAlign: 'bottom',
          },
        },
      },
    ],
  },

})
export default ({ symbol, quote, search }) => {
  const [peers] = useRequest(() => getPeers({ search }))

  const [details] = useRequest(() => getDetails({
    from: new Date().getTime() - (isMarketClosed(quote)
      ? (2 * 24 * 60 * 60 * 1000)
      : (1 * 24 * 60 * 60 * 1000)),
    to: new Date().getTime(),
    ticker: search,
    type: isMarketClosed(quote) ? 'day' : 'hour',
  }))
  const isClosed = isMarketClosed(quote)
  console.log(isClosed)

  console.log(details)
  return peers && details ? <div className={'row'}>
    <div className="col-md-6">
      <p>High Price:{quote.h}</p>
      <p>Low Price:{quote.l}</p>
      <p>Open Price:{quote.o}</p>
      <p>Prev. Close:{quote.pc}</p>
      <h2><a
        className={'text-sm-end'}
        href={symbol.weburl}
        target={'_blank'}
      >About the company</a></h2>
      <p>IPO Start Date:{symbol.ipo}</p>
      <p>Industry:{symbol.finnhubIndustry}</p>
      <p>Webpage:{symbol.weburl}</p>
      <p>Company peers</p>
      {peers && <div className={'btn btn-link'}>
        {peers.join(', ')}
      </div>}
    </div>
    <div className={'col-md-6'}>
      <HighchartsReact
        highcharts={Highcharts}
        options={chartOptions(search, details, quote)}
      />
    </div>
  </div> : <div className="spinner-border text-primary" role="status">
    <span className="visually-hidden">Loading...</span>
  </div>
}
