import './App.css'
import {
  Autocomplete,
  CircularProgress,
  debounce,
  TextField,
} from '@mui/material'
import { Fragment, useEffect, useRef, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { getTicket } from '../../api'

function sleep(duration) {
  return new Promise((resolve) => {
    setTimeout(() => {
      resolve()
    }, duration)
  })
}

function Index({ children, ...props }) {
  const [open, setOpen] = useState(false)
  const [options, setOptions] = useState([])
  const [loading, setLoading] = useState(false)
  const [inputValue, setInputValue] = useState('')
  const currentInputRef = useRef('')
  const navigate = useNavigate()
  const handleInput = async(e) => {
    setLoading(true) // Start loading state
    const currentValue = e.target.value.toString()
    setInputValue(currentValue)
    currentInputRef.current = currentValue
    const data = await getTicket({ search: e.target.value })
    if (currentValue !== currentInputRef.current) return
    setOptions(data) // Update options with fetched data
    setLoading(false) // End loading state
  }
  return (
    <div className={'container text-center pt-4'}>
      <h2>STOCK SEARCH</h2>
      <div className="input-group w-50 mx-auto position-relative">
        {/*style={{
            position: 'absolute',
            width: '50%',
            opacity: 0,
          }}*/}
        <Autocomplete
          style={{ width: '100%' }}
          id="asynchronous-demo"
          open={open}
          disableClearable={false}
          onOpen={() => {
            setOpen(true)
          }}
          onClose={() => {
            setOpen(false)
          }}
          isOptionEqualToValue={(
            option,
            value,
          ) => option.symbol === value.symbol}
          getOptionLabel={(option) => option.symbol}
          options={options}
          loading={loading}
          inputValue={inputValue}
          onInput={handleInput}
          loadingText={<CircularProgress color="inherit" size={20} />}
          renderInput={(params) => (
            <TextField{...params} label="Stock ticker"
                      placeholder={'Enter stock ticker symbol'}
            />
          )}
        />

        <i
          className={'bi bi-search position-absolute'}
          onClick={() => navigate(`/search/${inputValue}`)}
          style={{ right: 70, top: 15 }}
        ></i>
      </div>
      {children}
    </div>
  )
}

export default Index
