import { useEffect, useRef } from 'react'
import { getDetails, getNews } from '../../api'
import useRequest from '../../hook/useRequest'

const createOptions = (dom, data) => {

  // split the data set into ohlc and volume
  const ohlc = [],
    volume = [],
    dataLength = data.length,
    // set the allowed units for data grouping
    groupingUnits = [
      [
        'week',                         // unit name
        [1],                             // allowed multiples
      ], [
        'month',
        [1, 2, 3, 4, 6],
      ],
    ]

  for (let i = 0; i < dataLength; i += 1) {
    ohlc.push([
      data[i][0], // the date
      data[i][1], // open
      data[i][2], // high
      data[i][3], // low
      data[i][4], // close
    ])

    volume.push([
      data[i][0], // the date
      data[i][5], // the volume
    ])
  }
  window.Highcharts.stockChart(dom, {
      chart: {
        backgroundColor: '#F4F4F4', // Set the background color here
      },
      rangeSelector: {
        selected: 2,
      },

      title: {
        text: 'AAPL Historical',
      },

      subtitle: {
        text: 'With SMA and Volume by Price technical indicators',
      },

      yAxis: [
        {
          startOnTick: false,
          endOnTick: false,
          labels: {
            align: 'right',
            x: -3,
          },
          title: {
            text: 'OHLC',
          },
          height: '60%',
          lineWidth: 2,
          resize: {
            enabled: true,
          },
        }, {
          labels: {
            align: 'right',
            x: -3,
          },
          title: {
            text: 'Volume',
          },
          top: '65%',
          height: '35%',
          offset: 0,
          lineWidth: 2,
        },
      ],

      tooltip: {
        split: true,
      },

      plotOptions: {
        series: {
          dataGrouping: {
            units: groupingUnits,
          },
        },
      },

      series: [
        {
          type: 'candlestick',
          name: 'AAPL',
          id: 'aapl',
          zIndex: 2,
          data: ohlc,
        }, {
          type: 'column',
          name: 'Volume',
          id: 'volume',
          data: volume,
          yAxis: 1,
        }, {
          type: 'vbp',
          linkedTo: 'aapl',
          params: {
            volumeSeriesID: 'volume',
          },
          dataLabels: {
            enabled: false,
          },
          zoneLines: {
            enabled: false,
          },
        }, {
          type: 'sma',
          linkedTo: 'aapl',
          zIndex: 1,
          marker: {
            enabled: false,
          },
        },
      ],
    },
  )
  // create the chart
}
export default ({ search }) => {
  const [details] = useRequest(() => getDetails({
    from: new Date().getTime() - (365 * 2 * 24 * 60 * 60 * 1000),
    to: new Date().getTime(),
    ticker: search,
  }))
  const domRef = useRef()
  useEffect(() => {
    details && createOptions(domRef.current, details.map(item => ([
      item.t,
      item.o,
      item.h,
      item.l,
      item.c,
      item.v,
    ])))
  }, [details])

  return details ? <div style={{ height: 800 }} ref={domRef}></div> :
    <div className="spinner-border text-primary" role="status">
      <span className="visually-hidden">Loading...</span>
    </div>
}
