import { getNews } from '../../api'
import useRequest from '../../hook/useRequest'

const NewsModal = ({ item }) => {
  return <div
    className="modal fade"
    id="NewsModal"
    tabIndex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    <div className="modal-dialog">
      <div className="modal-content">
        <div className="modal-header">
          <div className="modal-title fs-5" id="exampleModalLabel">
            <div className={'h4'}>{item.related}</div>
            <div className={'h6 text-start'}>${item.datetime}</div>
          </div>
          <button
            type="button"
            className="btn-close" data-bs-dismiss="modal" aria-label="Close"
          ></button>
        </div>
        <div className="modal-body">
          <h2>{item.headline}</h2>
          <p>{item.summary}</p>
          <p>For more details click <a
            href={item.url}
            target={'_blank'}
          >here</a></p>
          <div
            className="card"
          >
            <div className="card-body">
              <p className={'text-start'}>Share</p>
              <div className="d-flex align-items-center gap-4">
                <img onClick={()=>shareOnTwitter(item.url,item.summary)} src={'data:image/png;base64,'} width={'30px'} height={'30px'} />
                <img onClick={()=>shareOnFacebook(item.url,item.summary)} src={'data:image/jpeg;base64,'} width={'30px'} height={'30px'} />
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>
  </div>

}

function shareOnTwitter(url, title) {
  const twitterURL = `https://twitter.com/intent/tweet?url=${encodeURIComponent(url)}&text=${encodeURIComponent(title)}`;
  window.open(twitterURL, '_blank');
}

function shareOnFacebook(url) {
  const facebookURL = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(url)}`;
  window.open(facebookURL, '_blank');
}

const News = ({ item }) => {
  /*{
      "category": "company",
      "datetime": 1711662276,
      "headline": "IVV: The S&P 500 Is Not Expensive",
      "id": 126746096,
      "image": "https://static.seekingalpha.com/cdn/s3/uploads/getty_images/1328821143/image_1328821143.jpg?io=getty-c-w1536",
      "related": "AAPL",
      "source": "SeekingAlpha",
      "summary": "iShares Core S&P 500 ETF (IVV) is an ideal investment option with balanced growth potential and a low fee percentage. Read more on IVV ETF here,",
      "url": "https://finnhub.io/api/news?id=115667362d3515c4406ff8287d73ef588c8e28c426e2b2e27defd51ffc17c084"
  }*/
  return <div
    className="card col-md-6"
    data-bs-toggle="modal"
    data-bs-target="#NewsModal"
  >
    <div className="card-body">
      <div className="d-flex align-items-center">
        <img src={item.image} width={'80px'} height={'50px'} />
        <p className={'mb-0 text-center flex-grow-1'}>{item.headline}</p>
      </div>
    </div>
    <NewsModal item={item} />
  </div>
}

export default ({ search }) => {
  const [news] = useRequest(() => getNews({
    from: new Date().getTime() - (7 * 24 * 60 * 60 * 1000),
    to: new Date().getTime(),
    ticker: search,
  }))

  return news ? <div className={'row g-3'}>
    {
      news.map(newItem => <News key={newItem.id} item={newItem}></News>)
    }
  </div> : <div className="spinner-border text-primary" role="status">
    <span className="visually-hidden">Loading...</span>
  </div>
}
