import { Box, Tab, Tabs } from '@mui/material'
import { useRef, useState } from 'react'
import { useParams } from 'react-router-dom'
import {
  getQuote,
  getStock,
  getSymbol,
  getUser,
  getWatch, postBuy,
  postWatch,
} from '../../api'
import UseRequest from '../../hook/useRequest'
import Home from '../Home'
import Charts from './Charts'
import Insights from './Insights'
import Summary from './Summary'
import TopNews from './TopNews'

function a11yProps(index) {
  return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
  }
}

function TabPanel(props) {
  const { children, value, index, ...other } = props

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`full-width-tabpanel-${index}`}
      aria-labelledby={`full-width-tab-${index}`}
      {...other}
    >
      {value === index && (
        children
      )}
    </div>
  )
}

export const isMarketClosed = (stockDetails) => {
  const currentTime = Math.floor(Date.now() / 1000) // Get the current timestamp in seconds
  const lastTimestamp = Math.floor(new Date(stockDetails.t).getTime() / 1000) // Get the last timestamp from the stock details
  const elapsedMinutes = Math.floor((currentTime - lastTimestamp) / 60) // Calculate the elapsed minutes
  if (elapsedMinutes > 5) {
    return true // Market is closed
  } else {
    return false // Market is open
  }
}

export default () => {
  let { ticker } = useParams()
  const [symbol] = UseRequest(() => getSymbol({ search: ticker }))
  const [quote] = UseRequest(() => getQuote({ search: ticker }))

  const isLoading = !symbol || !quote
  return <Home>
    {isLoading ? <div className="spinner-border text-primary" role="status">
      <span className="visually-hidden">Loading...</span>
    </div> : <TicketPage
      symbol={symbol}
      quote={quote}
      search={ticker}
    />}
  </Home>
}
const TicketPage = ({ symbol, quote, search }) => {
  const [value, setValue] = useState(0)
  const [watch, updateWatch] = UseRequest(() => getWatch())
  const [stock] = UseRequest(() => getStock())
  const [showBuy, setShowBuy] = useState(false)
  const handleChange = (event, newValue) => {
    setValue(newValue)
  }
  const handleUpdate = () => {
    postWatch({
      name: symbol.ticker,
      turnon: watch && watch.find(item => item.name === symbol.ticker) && watch.find(
        item => item.name === symbol.name).turnon ? 0 : 1,
    }).then(() => updateWatch())

  }
  const isClosed = isMarketClosed(quote)
  return <>
    {
      showBuy && <div className="alert alert-success" role="alert">
        {symbol.name} bought successfully
        <button
          type="button"
          onClick={() => setShowBuy(false)}
          className="btn-close float-end"
        ></button>
      </div>
    }
    <div className={'container'}>
      <div className={'d-flex'}>
        <div className={'w-100'}>
          <h3>{symbol.ticker}<i
            className={`bi bi-star-fill ${watch
              ? watch.find(item => item.name === symbol.ticker)
                ? watch.find(item => item.name === symbol.ticker).turnon
                  ? 'text-warning'
                  : ''
                : ''
              : ''}`}
            onClick={() => handleUpdate()}
          ></i></h3>
          <h4>{symbol.name}</h4>
          <p>{symbol.exchange}</p>
          <button
            className={'btn btn-success'}
            data-bs-toggle="modal"
            data-bs-target="#StockModal"
          >buy
          </button>
          {
            stock && stock.find(item => item.name === symbol.ticker) && <button
              className={'btn btn-danger'}
              data-bs-toggle="modal"
              data-bs-target="#SellModal"
            >sell
            </button>
          }
        </div>
        <div className={'w-100'}>
          <img width={100} height={100} src={symbol.logo} />
        </div>
        <div className={'w-100'}>
          <h3
            className={quote.d < 0
              ? 'text-danger'
              : 'text-success'}
          >{quote.c}</h3>
          <h4
            className={quote.d < 0
              ? 'text-danger'
              : 'text-success'}
          >{quote.d}({quote.dp})</h4>
          <p>{quote.t}</p>
        </div>
      </div>
      {
        isClosed ? <div className={'d-flex justify-content-center'}>
          <p className={'text-danger'}>Market Closed on {quote.t}</p>
        </div> : <div className={'d-flex justify-content-center'}>
          <p className={'text-success'}>Market is Open</p>
        </div>
      }
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
        <Tabs
          onChange={handleChange}
          variant={'scrollable'}
          value={value}
          aria-label="basic tabs example"
          allowScrollButtonsMobile
          scrollButtons
        >
          <Tab
            style={{ flex: 1, whiteSpace: 'nowrap' }} {...a11yProps(0)}
            label="Summary"
          />
          <Tab
            style={{ flex: 1, whiteSpace: 'nowrap' }} {...a11yProps(1)}
            label="Top News"
          />
          <Tab
            style={{ flex: 1, whiteSpace: 'nowrap' }} {...a11yProps(2)}
            label="Charts"
          />
          <Tab
            style={{ flex: 1, whiteSpace: 'nowrap' }} {...a11yProps(3)}
            label="Insights"
          />
        </Tabs>
      </Box>
      <TabPanel value={value} index={0}>
        <Summary symbol={symbol} quote={quote} search={search}></Summary>

      </TabPanel>
      <TabPanel value={value} index={1}>
        <TopNews search={search} />
      </TabPanel>
      <TabPanel value={value} index={2}>
        <Charts search={search}></Charts>
      </TabPanel>
      <TabPanel value={value} index={3}>
        <Insights search={search} symbol={symbol}></Insights>
      </TabPanel>
    </div>
    <StockModal
      symbol={symbol}
      quote={quote}
      onSuccess={() => setShowBuy(true)}
    ></StockModal>
    <StockModal symbol={symbol} quote={quote} isSelling></StockModal>

  </>
}

export const StockModal = ({ isSelling, symbol, quote, onSuccess }) => {
  const [user] = UseRequest(() => getUser())
  const [q, setQ] = useState(0)
  const buttonRef = useRef()
  const handleBuy = () => {
    postBuy({
      name: symbol.ticker,
      price: quote.c,
      quantity: q,
      userPrice: user.price,
    }).then(() => {
      buttonRef.current.click()
      onSuccess()
    })
  }
  return <div
    className="modal fade"
    id={isSelling ? 'SellModal' : 'StockModal'}
    tabIndex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
  >
    {user && symbol && <div className="modal-dialog">
      <div className="modal-content">
        <div className="modal-header">
          <h1
            className="modal-title fs-5"
            id="exampleModalLabel"
          >{symbol.name}</h1>
          <button
            type="button"
            ref={buttonRef}
            className="btn-close" data-bs-dismiss="modal" aria-label="Close"
          ></button>
        </div>
        <div className="modal-body">
          <p>Current Price: {quote.c}</p>
          <p>Money In Wallet: {user.price}</p>
          <p className={'d-flex'}><span>Quantity: </span><input
            className={'form-control'}
            type={'number'}
            value={q}
            onInput={(e) => setQ(e.target.value)}
          /></p>

          {
            Number(q) * quote.c > user.price &&
            <p className={'text-danger'}>not enough money in wallet</p>
          }

        </div>
        <div className="modal-footer  justify-content-between">

          <span>Total:{Number(q) * Number(quote.c)}</span>
          <button
            type="button"
            onClick={() => handleBuy()}
            className="btn btn-success"
          >{
            isSelling ? 'Sell' : 'Buy'
          }</button>

        </div>
      </div>
    </div>}
  </div>

}
